/* General page styling */
.documentation-page {
    background-color: #0D0D0D;
    color: #FFE9D4;
    font-family: 'IBM Plex Mono', monospace;
    padding: 20px;
    min-height: 70vh;

    /* Background image settings */
    background-image: url('../../assets/bg5.jpg');
    /* Replace with your image path */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
}

/* Content container */
.documentation-content {
    max-width: 900px;
    margin: auto;
    padding: 20px;
}

/* Headings */
.documentation-content h1 {
    font-size: 3rem;
    text-align: center;
    color: #00FF00;
    margin-bottom: 20px;
    border-bottom: 2px solid #00FF00;
    padding-bottom: 10px;
}

.documentation-content h2 {
    font-size: 2rem;
    color: #F0DAFF;
    margin-top: 30px;
}

.documentation-content h3 {
    font-size: 1.5rem;
    color: #EDEFEE;
    margin-top: 20px;
}

/* Paragraph styling */
.documentation-content p {
    font-size: 1.5rem;
    line-height: 1.6;
    margin-bottom: 15px;
}

/* Scan section with vertical layout */
.doc-scan-section {
    background-color: #1A1A1A;
    padding: 15px;
    border-radius: 8px;
    margin-top: 20px;
    display: flex;
    display: flex;
    flex-direction: column;
    /* Ensures everything stacks vertically */
}

/* Code block styling */
pre {
    background-color: #262626;
    color: #00FF00;
    padding: 10px;
    border-radius: 5px;
    font-size: 0.9rem;
    overflow-x: auto;
    margin-top: 10px;
}

/* Usage styling */
.usage {
    font-weight: bold;
    margin-top: 10px;
    color: #FFE9D4;
}

/* Ending line after each section */
.doc-scan-section:not(:last-child) {
    border-bottom: 2px solid #FFE9D4;
    padding-bottom: 20px;
}